body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: Roboto, sans-serif;  
}

body{
  position: relative;
  margin: 0;
}

.followed{
  background-color: blue;
}

.navbar{
  height: 7vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.adminnav{
  height: 7vh;
  width: 50vw;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.header{
  height: 18vh;
  min-height: 170px;
  display: flex;
  justify-content: space-between;
  border-bottom: black solid 2px;
}

.header > img{
  width: 350px;
  height: 200px;
  background: transparent;
}

.main{
  height: 100vh;
  width: 100vw;
  background: linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  overflow-y: auto;
}

.logindiv{
  border: rgba(0, 0, 0, 0.438) solid 2px;
  border-radius: 20px;
  background: rgb(218,218,218);
  background: linear-gradient(90deg, rgba(218,218,218,1) 0%, rgba(255,255,255,1) 50%, rgba(195,195,195,1) 100%);
  height: 50%;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.centerdiv{
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
  background-attachment: fixed;
  box-shadow: '0 3px 5px 2px rgba(255, 105, 135, .3)';
  color: 'white';
}

.div{
  margin-top: 10px;
  margin-right: 20px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 22vw;
}

.footer{
  position: fixed;
  bottom: 0%;
  background-color: black;
  color: white;
  width: 100vw;
}

.listcomponent{
  max-width: 350px;
  min-width: 300px;
}

#listcont{
  margin-top: 10px;
  width: 100vw;
  display: flex;
  justify-content: center;
}

#listcont > *{
  max-width: 95vw;
}

.form{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  margin-left: 10vw;
  width: 80vw;
  border: black solid 2px;
  border-radius: 20px;
  background: white;
  margin-top: 10px;
}

.formsep{
  margin-left: 10px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.formdate{
  margin-left: 10px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  width: 50vw;
}

#description{
  width: 70vw;
}

.mb{
  margin-bottom: 100px;
}

.chartcontainer{
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  background: white;

}

.ch{
  width: 90vw;
  margin-bottom: 5vh;
}

.fl{
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
}